<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区 -->
    <el-card>
      <!-- 搜索区 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            v-model="queryInf.query"
            clearable
            @clear="getGoodsList()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodsList()"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="$router.push('/goods/add')"
            >添加商品</el-button
          >
        </el-col>
      </el-row>
      <!-- 商品数据表格 -->
      <el-table
        :data="goodsList"
        border
        stripe
        :header-cell-style="{ textAlign: 'center' }"
        :cell-style="{ textAlign: 'center' }"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="700px">
        </el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)">
        </el-table-column>
        <el-table-column prop="goods_weight" label="商品重量">
        </el-table-column>
        <el-table-column label="创建时间">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              @click="getGoodsInf(scope.row.goods_id)"
            >
              编辑
            </el-button>
            <el-button
              type="danger"
              size="mini"
              @click="removeGoodById(scope.row.goods_id)"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInf.pagenum"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="queryInf.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 修改商品信息对话框 -->
    <el-dialog
      title="修改商品信息"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editGoodsForm"
        :rules="editGoodsFormRules"
        ref="editGoodsFormRef"
        label-width="100px"
      >
        <el-form-item label="商品名称" prop="goods_name">
          <el-input v-model.trim="editGoodsForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" prop="goods_price">
          <el-input v-model.number="editGoodsForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="商品重量" prop="goods_weight">
          <el-input v-model.number="editGoodsForm.goods_weight"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editGoods">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getGoodsList()
  },
  data() {
    return {
      // 商品数据
      goodsList: [],
      // 查询参数
      queryInf: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 数据总数
      total: null,
      // 编辑商品信息对话框出现
      editDialogVisible: false,
      // 编辑商品信息表单
      editGoodsForm: {},
      // 编辑商品信息表单验证规则
      editGoodsFormRules: {
        goods_name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
          { type: 'number', message: '商品价格必须为数字值' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' },
          { type: 'number', message: '商品重量必须为数字值' }
        ]
      }
    }
  },
  methods: {
    // 获取商品数据列表
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInf
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品数据列表失败')
      }
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    // 监听页面数据数量变化
    handleSizeChange(val) {
      this.queryInf.pagesize = val
      this.getGoodsList()
    },
    // 监听分页页面变化
    handleCurrentChange(val) {
      this.queryInf.pagenum = val
      this.getGoodsList()
    },
    // 根据ID删除商品数据
    async removeGoodById(goodsId) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该商品信息, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete(`goods/${goodsId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除商品信息失败')
      }
      this.$message.success('删除商品信息成功')
      this.getGoodsList()
    },
    // 编辑商品信息
    async getGoodsInf(goodsId) {
      this.editDialogVisible = true
      const { data: res } = await this.$http.get(`goods/${goodsId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品信息失败')
      }
      this.editGoodsForm = res.data
    },
    // 发送编辑后的商品信息
    editGoods() {
      this.$refs.editGoodsFormRef.validate(async valid => {
        if (valid) {
          this.editDialogVisible = false
          const { data: res } = await this.$http.put(
            `goods/${this.editGoodsForm.goods_id}`,
            this.editGoodsForm
          )
          if (res.meta.status !== 200) {
            return this.$message.error('修改商品信息失败')
          }
          this.$message.success('修改商品信息成功')
          this.getGoodsList()
        }
      })
    },
    editDialogClosed() {
      this.$refs.editGoodsFormRef.resetFields()
    }
  }
}
</script>

<style lang="less" scoped></style>
